/* Global */
[data-theme="claudion_theme"] {
  --main-color: #1cdeb0;
  --second-color: #ffffff;
  --third-color: #bcbec0;
  --fourth-color: #000000;

  --primary: var(--main-color);
  --dt-primary-color: var(--main-color);
  --blue-avatar-bg: var(--main-color);
  --blue-avatar-color: var(--main-color);
  --blue: var(--main-color);
}

[data-theme="claudion_theme"] .form-control {
  background-color: var(--second-color);
  color: var(--fourth-color);
}
/* [data-theme="claudion_theme"] .page-container, */
[data-theme="claudion_theme"] .page-container,
[data-theme="claudion_theme"] .sticky-top {
  background-color: #1cdeb0;
}

[data-theme="claudion_theme"] .page-head,
[data-theme="claudion_theme"] .widget {
  background-color: var(--fourth-color);
}

[data-theme="claudion_theme"] .layout-main-section {
  background-color: #1cdeb0;
}
[data-theme="claudion_theme"] ul li a:hover {
  background-color: #1cdeb0;
  color: #fff;
}
/* Main */
/* [data-theme="claudion_theme"] body {
  font-size: 14px;
  background-color: var(--second-color);
  color: var(--third-color);
} */
[data-theme="claudion_theme"] a {
  color: #1cdeb0;
  font-weight: 500;
}
[data-theme="claudion_theme"] .small,
small {
  font-size: 95%;
}
[data-theme="claudion_theme"] .h6.uppercase,
h6.uppercase {
  font-weight: 600;
  font-size: 11.5px;
}

/* Navigation Bar */
/* [data-theme="claudion_theme"] .standard-sidebar-item.selected {
  background: var(--main-color);
} */
[data-theme="claudion_theme"] .standard-sidebar-item.selected a {
  color: #1cdeb0;
}
[data-theme="claudion_theme"] .standard-sidebar-item.selected a svg {
  fill: #1cdeb0;
}
[data-theme="claudion_theme"] .standard-sidebar-item:hover {
  background-color: #1cdeb0;
}
[data-theme="claudion_theme"] .standard-sidebar-item:hover a,
[data-theme="claudion_theme"] .standard-sidebar-item:hover svg {
  color: var(--fourth-color);
  fill: #fff;
}

[data-theme="claudion_theme"] .navbar {
  background: var(--second-color) !important;
  color: var(--fourth-color);
  border: none !important;
}
[data-theme="claudion_theme"] .search-bar {
  background: var(--second-color) !important;
  color: var(--fourth-color);
  border: none !important;
}

[data-theme="claudion_theme"] .navbar-nav > li > a {
  color: #000000 !important;
}
[data-theme="claudion_theme"] .navbar-nav > li > a:hover {
  color: var(--third-color) !important;
}
[data-theme="claudion_theme"] .navbar-brand:hover {
  color: var(--third-color) !important;
}

/* Widgets */
/* [data-theme="claudion_theme"] .widget {
  background-color: var(--widget-bg);
  border: none;
  box-shadow: 0px 3px 12px 0px var(--widget-bg), 0 3px 3px -2px #1cdeb014;
} */
[data-theme="claudion_theme"]
  .widget.shortcut-widget-box
  .widget-head
  /* .widget-title */
  i {
  color: var(--third-color);
}

[data-theme="claudion_theme"] .widget-title {
  color: #1cdeb0;
}

[data-theme="claudion_theme"]
  .widget.shortcut-widget-box
  .widget-head
  .widget-title:hover
  span {
  color: #1cdeb0;
}
[data-theme="claudion_theme"] .widget-body .link-item:hover {
  color: #fff;
  background: #1cdeb0;
}
/* Sidebar */
[data-theme="claudion_theme"] .desk-sidebar .desk-sidebar-item {
  font-size: 13px;
  font-weight: 600;
  color: var(--third-color);
}
[data-theme="claudion_theme"] .layout-side-section {
  background: var(--fourth-color);
  font-size: 14px;
}
[data-theme="claudion_theme"] .uppercase {
  color: var(--third-color);
}

/* Dropdowns */
[data-theme="claudion_theme"] .dropdown-menu > li > a {
  font-size: 13px;
  color: var(--third-color);
  font-weight: 500;
}
[data-theme="claudion_theme"] .awesomplete > ul > li[aria-selected="true"] {
  background-color: transparent;
}
[data-theme="claudion_theme"] .awesomplete > ul > li:hover {
  background: #1cdeb0;
}
[data-theme="claudion_theme"] .awesomplete > ul > li:hover a {
  color: #fff;
}
/* Page Heading */
/* [data-theme="claudion_theme"] .page-head {
  border-bottom: none !important;
  box-shadow: 0 1px 10px 1px rgba(28, 222, 176, 0.1);
} */
[data-theme="claudion_theme"] .page-title .title-text {
  color: #1cdeb0;
  font-size: 22px;
}
[data-theme="claudion_theme"] .widget .widget-head .widget-title,
[data-theme="claudion_theme"] .section-head,
[data-theme="claudion_theme"] .comment-input-header {
  font-size: 14px;
  color: #1cdeb0;
}

/*Main Section */
[data-theme="claudion_theme"] .list-row-head {
  background-color: #000000;
  /* border-bottom: 1px solid var(--main-color) !important; */
}

/* Buttons */
[data-theme="claudion_theme"] .btn-primary {
  background-color: #1cdeb0;
  border-color: #1cdeb0;
}

[data-theme="claudion_theme"] .btn-secondary {
  background-color: #1cdeb0;
  color: black;
}

[data-theme="claudion_theme"] .btn-primary:hover,
[data-theme="claudion_theme"] .btn-primary:focus {
  background-color: var(--third-color);
  border-color: var(--third-color);
  color: #fff !important;
}

[data-theme="claudion_theme"] .btn-default:hover,
[data-theme="claudion_theme"] .btn-default:focus {
  background-color: var(--second-color);
  border-color: var(--second-color);
  color: var(--third-color) !important;
}

/* Other Components */
[data-theme="claudion_theme"] input[type="checkbox"]:checked:before {
  color: white;
}
[data-theme="claudion_theme"] input[type="checkbox"]:checked {
  background: #1cdeb0;
}
[data-theme="claudion_theme"] .document-link-badge span {
  background-color: #1cdeb0 !important;
  color: #fff;
}
